import React, { Component } from "react";
import Slider from "react-slick";
import { portfolioSlick} from "../../script";


const PortfolioList = [
    {
        image: 'image-1',
        category: 'Webstrnka a logo',
        title: 'Woodland - Svet z Dreva'
    },
    {
        image: 'image-2',
        category: 'Webstrnka a logo',
        title: 'Strojov potery Jozef Babinsk'
    },
    {
        image: 'image-5',
        category: 'Webstrnka',
        title: 'Saldom elektro'
    },
    {
        image: 'image-4',
        category: 'Webstrnka',
        title: 'Nerezov vroba Tom Murn'
    },
    {
        image: 'image-6',
        category: 'Webstrnka',
        title: ' V projekt'
    },
    {
        image: 'image-3',
        category: 'Webstrnka',
        title: 'Nbytok Orava'
    }
]

class Portfolio extends Component{
    render(){
        let title = 'Nae projekty',
        description = 'Krtka ukka niektorch z naich diel.';
        return(
            <React.Fragment>
                <div className="portfolio-wrapper">
                    <div className="container">
                        <div className="row">
                            <div className="col-lg-6">
                                <div className="section-title">
                                    <h2>{title}</h2>
                                    <p>{description}</p>
                                </div>
                            </div>
                        </div>
                    </div>
                    <div className="portfolio-slick-activation mt--70 mt_sm--40">
                        <Slider {...portfolioSlick}>
                            {PortfolioList.map((value , index) => (
                                <div className="portfolio" key={index}>
                                    <div className="thumbnail-inner">
                                        <div className={`thumbnail ${value.image}`}></div>
                                        <div className={`bg-blr-image ${value.image}`}></div>
                                    </div>
                                    <div className="content">
                                        <div className="inner">
                                            <p>{value.category}</p>
                                            <h4><a href="/portfolio-details">{value.title}</a></h4>
                                            <div className="portfolio-button">
                                                <a className="rn-btn" href="/portfolio-details">Zobrazi</a>
                                            </div>
                                        </div>
                                    </div>
                                </div>
                            ))}
                        </Slider>
                    </div>
                </div>
            </React.Fragment>
        )
    }
}
export default Portfolio;