import React, { Component } from "react";

class About extends Component{
    render(){
        let title = 'O ns',
        description = 'Sme nov inovatvna firma, zaoberajca sa tvorbou webovch strnok, e-shopov, vvojom webovch, i mobilnch aplikci.\n alej do nho spektra sluieb patr optimalizcia pre vyhadvacie nstroje, marketingov mentoring a v neposlednom rade kompletn kla grafickch sluieb.';
        return(
            <React.Fragment>
                <div className="about-wrapper">
                    <div className="container">
                        <div className="row row--35 align-items-center">

                            <div className="col-lg-5 col-md-12">
                                <div className="thumbnail">
                                    <img className="w-100" src="/assets/images/about/about-1.jpg" alt="About Images"/>
                                </div>
                            </div>

                            <div className="col-lg-7 col-md-12">
                                <div className="about-inner inner">
                                    <div className="section-title">
                                        <h2 className="title theme-gradient">{title}</h2>
                                        <p className="description">{description}</p>
                                    </div>
                                    
                                    <div className="row mt--30 mt_sm--10">
                                        <div className="col-lg-6 col-md-12 col-sm-12 col-12">
                                            <div className="about-us-list">
                                                <h3 className="title ">Vek je len slo</h3>
                                                <p>To, e sme na trhu tak krtko, ete neznamen e nemme iadne sksenosti a znalosti. Prve naopak, priname modern rieenia a sluby svetovch kvalt.</p>
                                            </div>
                                        </div>
                                        <div className="col-lg-6 col-md-12 col-sm-12 col-12">
                                            <div className="about-us-list">
                                                <h3 className="title ">Drme s dobou</h3>
                                                <p>Vyuvame najnovie technolgie a aktulne dizajnov trendy pre vytvorenie <strong className="theme-gradient">absoltnej spokojnosti</strong>  nho klienta. </p>
                                            </div>
                                        </div>
                                        <div className="service-btn about-btn">
                                            <a className="btn-transparent rn-btn-dark" href="/about"><span className="text">Viac informci... </span></a>
                                        </div>  
                                    </div>
                                </div>
                            </div>
                        </div>
                    </div>
                </div>
            </React.Fragment>
        )
    }
}
export default About;