import React, { Component } from "react";
import TextLoop from "react-text-loop";

class Slider extends Component {
  render() {
    return (
        <div className="slider-activation">
            {/* Start Single Slide */}
            <div className="slide slide-style-1 slider-fixed--height d-flex align-items-center bg_image bg_image--4" data-black-overlay="6">
                
                    <div className="row">
                        <div className="col-lg-12">
                            <img className="logo" src="/assets/images/logo/webclient-dark-large.png" alt="Webclient"/>
                            <h1 className="title text-loop">
                                <TextLoop>
                                    <span className="theme-gradient"> Tvorba webstrnok</span>
                                    <span className="theme-gradient"> Dizajnov sluby</span>
                                    <span className="theme-gradient"> Online marketing</span>
                                </TextLoop>{" "}
                            </h1>
                            <ul className="social-list">
                                <li><a href="https://www.facebook.com/webclient.sk"><img className="social social1" src="/assets/images/icons/fb.png" alt="facebook"/></a></li>
                                <li><a href="https://www.instagram.com/webclient.sk"><img className="social social2" src="/assets/images/icons/ig.png" alt="instagram"/></a></li>
                                <li><a href="https://twitter.com/"><img className="social social3" src="/assets/images/icons/tw.png" alt="twitter"/></a></li>
                            </ul>
                        </div>
                    </div>
                
            </div> 
            {/* End Single Slide */}
        </div>
    );
  }
}
export default Slider;