import React, { Component } from "react";

const PortfolioListContent = [
    {
        url: "/portfolio-details1",
        image: 'image-1',
        category: 'Webstrnka a logo',
        title: 'Woodland - Svet z Dreva'
    },
    {
        url: "/portfolio-details2",
        image: 'image-2',
        category: 'Webstrnka a logo',
        title: 'Strojov potery Jozef Babinsk'
    },
    {
        url: "/portfolio-details3",
        image: 'image-3',
        category: 'Webstrnka',
        title: 'Nbytok Orava'
    },
    {
        url: "/portfolio-details4",
        image: 'image-4',
        category: 'Webstrnka',
        title: 'Nerezov vroba Tom Murn'
    },
    {
        url: "/portfolio-details5",
        image: 'image-5',
        category: 'Webstrnka',
        title: 'Saldom Electro'
    }
]

class PortfolioList extends Component{
    render(){
        const {column , styevariation } = this.props;
        const list = PortfolioListContent.slice(0 , this.props.item);
        return(
            <React.Fragment> 
                {list.map((value , index) => (
                    <div className={`${column}`} key={index}>
                        <div className={`portfolio ${styevariation}`}>
                            <div className="thumbnail-inner">
                                <div className={`thumbnail ${value.image}`}></div>
                                <div className={`bg-blr-image ${value.image}`}></div>
                            </div>
                            <div className="content">
                                <div className="inner">
                                    <p>{value.category}</p>
                                    <h4><a href="/portfolio-details">{value.title}</a></h4>
                                    <div className="portfolio-button">
                                        <a className="rn-btn" href={value.url}>Detaily</a>
                                    </div>
                                </div>
                            </div>
                        </div>
                    </div>
                ))}
               
            </React.Fragment>
        )
    }
}
export default PortfolioList;