import React, { Component , Fragment } from "react";
import CountUp from 'react-countup';
import VisibilitySensor from 'react-visibility-sensor';

class Counter extends Component{
    state = {
        didViewCountUp: false
    };
    onVisibilityChange = isVisible => {
        if (isVisible) {
            this.setState({didViewCountUp: true});
        }
    }
    render(){
        let Data = [
            {
                countNum : 5,
                countTitle: 'Spokojnch klientov, ktor zverili svoj online biznis do naich rk.',
            },
            {
                countNum : 8000,
                countTitle: 'Riadkov kdu pri kadom projekte.',
            },
            {
                countNum : 10,
                countTitle: 'Hodn prce denne investovanch do neustleho zlepovania naich sluieb.',
            },
        ];

        return(
            <Fragment>
                <div className="row">
                    {Data.map( (value, index) => (
                        <div className="counterup_style--1 col-lg-4 col-md-4 col-sm-6 col-12 " key={index}>
                            <h5 className="counter theme-gradient">
                                <VisibilitySensor onChange={this.onVisibilityChange} offset={{top:10}} delayedCall>
                                    <CountUp end={this.state.didViewCountUp ? value.countNum : 0} />
                                </VisibilitySensor>
                            </h5>
                            <p className="description">{value.countTitle}</p>
                        </div>
                    ))}
                </div>
            </Fragment>
        )
    }
}
export default Counter;

