import React, { Component } from "react";
import PageHelmet from "../../component/common/Helmet";
import Breadcrumb from "../../component/common/Breadcrumb";
import {  FiUsers , FiMonitor ,FiChevronUp, FiShoppingCart,  FiHeadphones, FiImage, FiSettings, FiPenTool, FiBookmark, FiSliders } from "react-icons/fi";
import ScrollToTop from 'react-scroll-up';
import Header from "../../component/header/Header";
import Footer from "../../component/footer/Footer";




const WebList = [
    {
        icon: <FiMonitor />,
        title: 'Prezenn strnka',
        description: 'Jednoduch webstrnka so vetkm, o potrebujete pre opis Vho podnikania.',
        price:'od 350'
    },
    {
        icon: <FiHeadphones />,
        title: 'Komunikan strnka',
        description: 'Potrebujete by v neustlom styku so zkaznkom prostrednctvom blogu alebou online chatu?',
        price:'od 500'
    },
    {
        icon: <FiShoppingCart />,
        title: 'Internetov obchod',
        description: 'Ponkame dkladn predstavenie produktu, prepracovan systm objednvok a ete omnoho viac.',
        price:'od 750'
    }
]

const DesignList = [
    {
        icon: <FiImage />,
        title: 'Tvorba Loga',
        description: 'I throw myself down among the tall grass by the stream as I lie close to the earth.',
        price:'od 100'
    },
    {
        icon: <FiPenTool />,
        title: 'Nvrh vizitiek a inch produktov',
        description: 'I throw myself down among the tall grass by the stream as I lie close to the earth.',
        price:'od 50'
    },
    {
        icon: <FiUsers />,
        title: 'Kompletn virtulna identita',
        description: 'I throw myself down among the tall grass by the stream as I lie close to the earth.',
        price:'od 150'
    },
  
]

const MarketingList = [
    {
        icon: <FiSettings />,
        title: 'SEO-optimalizcia',
        description: 'I throw myself down among the tall grass by the stream as I lie close to the earth.',
        price:'od 70'
    },
    {
        icon: <FiBookmark />,
        title: 'Reklamn nvrh',
        description: 'Navrhneme Vm propagan prspevok na socilne siete alebo reklamn spot pre investorov.',
        price:'od 100'
    },
    {
        icon: <FiSliders />,
        title: 'Biznis pln',
        description: 'Rozbiehate nov firmu a nemte iadne znalosti? Mme pre vs rieenie - N Biznis pln.',
        price:'od 170'
    },
    
]
class Service extends Component{
    render(){
        return(
            <React.Fragment>
                <PageHelmet pageTitle='Sluby' />
                <Header headertransparent="header--transparent" colorblack="color--black" logoname="logo.png" />

                {/* Start Breadcrump Area */}
                <Breadcrumb title={'Sluby'}   />
                {/* End Breadcrump Area */}

                {/* Start Service Area */}
                <div className="service-area ptb--120 bg_color--5">
                    <div className="container">
                        <div className="row">
                            <div className="col-lg-12">
                                <div className="section-title text-center mb--30">
                                    <h2>Tvorba webstrnok</h2>
                                    <p>Tvorenie webovch strnok patr medzi nae hlavn zameranie, preto verme, <br />  e aj vy zverte svoju strnku do naich rk.</p>
                                </div>
                            </div>
                        </div>
                        <div className="row service-one-wrapper">
                            {WebList.map( (val , i) => (
                                <div className="col-xl-4 col-lg-4 col-md-6 col-sm-6 col-12" key={i}>
                                    <a href="/service-details">
                                        <div className="service service__style--2">
                                            <div className="icon">
                                                {val.icon}
                                            </div>
                                            <div className="content">
                                                <h3 className="title">{val.title}</h3>
                                                <p>{val.description}</p>
                                                <p>{val.price}</p>
                                            </div>
                                        </div>
                                    </a>
                                </div>
                            ))}
                        </div>
                    </div>
                </div>
                {/* End Service Area */}

                {/* Start Service Area */}
                <div className="service-area ptb--120 bg_color--1">
                    <div className="container">
                        <div className="row">
                            <div className="col-lg-12">
                                <div className="section-title text-center mb--30">
                                    <h2>Grafick sluby</h2>
                                    <p>Precznos a zmysel pre detail s alfou a omegou spravneho dizajnra. <br /> Tm nam to samozrejme nechba :)</p>
                                </div>
                            </div>
                        </div>
                        <div className="row service-one-wrapper">
                            {DesignList.map( (val , i) => (
                                <div className="col-xl-4 col-lg-4 col-md-6 col-sm-6 col-12" key={i}>
                                    <a className="text-center" href="/service-details">
                                        <div className="service service__style--2">
                                            <div className="icon">
                                                {val.icon}
                                            </div>
                                            <div className="content">
                                                <h3 className="title">{val.title}</h3>
                                                <p>{val.description}</p>
                                                <p>{val.price}</p>
                                            </div>
                                        </div>
                                    </a>
                                </div>
                            ))}
                        </div>
                    </div>
                </div>
                {/* End Service Area */}

                {/* Start Service Area */}
                <div className="service-area creative-service-wrapper pt--90 pb--120 bg_color--5">
                    <div className="container">
                        <div className="row">
                            <div className="col-lg-12">
                                <div className="section-title text-center mb--30">
                                    <h2>Online marketing</h2>
                                    <p>Nedar sa vmu biznisu aj napriek tomu, e mte lukratvny produkt ? <br /> Problm bude .</p>
                                </div>
                            </div>
                        </div>
                        <div className="row creative-service">
                            {MarketingList.map( (val , i) => (
                                <div className="col-xl-4 col-lg-4 col-md-6 col-sm-6 col-12" key={i}>
                                    <a href="/service-details">
                                        <div className="service service__style--2">
                                            <div className="icon">
                                                {val.icon}
                                            </div>
                                            <div className="content">
                                                <h3 className="title">{val.title}</h3>
                                                <p>{val.description}</p>
                                                <p>{val.price}</p>
                                            </div>
                                        </div>
                                    </a>
                                </div>
                            ))}
                        </div>
                    </div>
                </div>
                {/* End Service Area */}

                {/* Start Back To Top */}
                <div className="backto-top">
                    <ScrollToTop showUnder={160}>
                        <FiChevronUp />
                    </ScrollToTop>
                </div>
                {/* End Back To Top */}
                
                <Footer />


            </React.Fragment>
        )
    }
}
export default Service;