import React, { Component } from "react";
import { Tab, Tabs, TabList, TabPanel } from 'react-tabs';
class Testimonial extends Component{
    render(){
        return(
            <React.Fragment>
                <div className="row">
                    <div className="col-lg-12">
                        <Tabs>
                            <TabPanel>
                                <div className="rn-testimonial-content text-center">
                                    <div className="inner">
                                        <p>Strnku som potreboval aby o najlepie odprezentovala moje sluby pred klientami v zahrani. Webclientu sa to podarilo nadmieru kvalitne splni a ete aj v prekvapivo <strong className="theme-gradient">krtkom ase.</strong></p>
                                    </div>
                                    <div className="author-info">
                                        <h6><span>Dominik alata </span> - SALDOM S.R.O</h6>
                                    </div>
                                </div>
                            </TabPanel>

                            <TabPanel>
                                <div className="rn-testimonial-content text-center">
                                    <div className="inner">
                                        <p>Chalani sa so strnkou naozaj vyhrali, animcie, tiene, proste vetko na o si len pomyslte. <br/> <strong className="theme-gradient">Kvalita za dobr cenu.</strong>  </p>
                                    </div>
                                    <div className="author-info">
                                        <h6><span>Jozef Babinsk </span> - STROJOV POTERY JOZEF BABINSK</h6>
                                    </div>
                                </div>
                            </TabPanel>
                            <TabPanel>
                                <div className="rn-testimonial-content text-center">
                                    <div className="inner">
                                        <p>Mnoho zkaznkov si ptalo kontakt na ma prostrednctvom socilnych siet. Tak som sa rozhodol, e informcie zhrniem na jednom mieste. <br/> <strong className="theme-gradient">Webstrnke od Webclientu !</strong></p>
                                    </div>
                                    <div className="author-info">
                                        <h6><span>RBERT KME </span> - WOODLAND.SK</h6>
                                    </div>
                                </div>
                            </TabPanel>

                            <TabPanel>
                                <div className="rn-testimonial-content text-center">
                                    <div className="inner">
                                        <p>V dnenej dobre je webstrnka pomaly nutnos pri podnikan. Preto som rd, e som to zveril Webclientu a som <strong className="theme-gradient">nadmieru spokojn.</strong></p>
                                    </div>
                                    <div className="author-info">
                                        <h6><span>TOM MURN </span> - NEREZOV VROBA TOM MURN</h6>
                                    </div>
                                </div>
                            </TabPanel>
                            
                            <TabList className="testimonial-thumb-wrapper">
                                <Tab>
                                    <div className="testimonial-thumbnai">
                                        <div className="thumb">
                                            <img src="/assets/images/client/testimonial-1.jpg" alt="Testimonial Images"/>
                                        </div>
                                    </div>
                                </Tab>
                                <Tab>
                                    <div className="testimonial-thumbnai">
                                        <div className="thumb">
                                            <img src="/assets/images/client/testimonial-2.jpg" alt="Testimonial Images"/>
                                        </div>
                                    </div>
                                </Tab>
                                <Tab>
                                    <div className="testimonial-thumbnai">
                                        <div className="thumb">
                                            <img src="/assets/images/client/testimonial-3.jpg" alt="Testimonial Images"/>
                                        </div>
                                    </div>
                                </Tab>
                                <Tab>
                                    <div className="testimonial-thumbnai">
                                        <div className="thumb">
                                            <img src="/assets/images/client/testimonial-4.jpg" alt="Testimonial Images"/>
                                        </div>
                                    </div>
                                </Tab>
                            </TabList>
                        </Tabs>
                    </div>
                </div>
            </React.Fragment>
        )
    }
}
export default Testimonial;